.button {
  display: block;
  width: 233px;
  min-height: 61px;
  margin: 0;
  padding: 0;
  padding-top: 20px;
  border: 4px solid $color-white;

  font-family: $font-text;
  @include font (12px, 14px);
  color: $color-white;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  outline: none;

  &:hover,
  &:focus {
    color: $color-text;
    background-color: $color-white;
    cursor: pointer;
    outline: none;
  }

  &:active {
    color: rgba($color-text, 0.3);
    outline: none;
  }
}

button[disabled] {
  opacity: 0.6;
  cursor: not-allowed;
}

