.main-nav {
  width: 97%;
  max-width: 1160px;
  min-height: 56px;
  margin: 0 auto;
  padding-top: 27px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $color-blue;
}

@media (max-width: $tablet-width) {
  .main-nav {
    width: 88%;
  }
}

@media (max-width: $mobile-width) {
  .main-nav {
    width: 88%;
  }
}
