$tablet-width: 1199px;
$mobile-width: 767px;
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

$font-title: "Bebas Neue", "Arial", sans-serif;
$font-text: "Roboto Condensed", "Arial", sans-serif;

$color-white: #FFFFFF;
$color-black: #000000;
$color-text: #444444;
$color-blue:#003D85;

@mixin font ($size, $line) {
  font-size: $size;
  line-height: $line;
}

@mixin list-reset () {
  margin: 0;
  padding: 0;
  list-style: none;
}

